@media screen and (max-width:960px) {


    /* shrink input box */
    .calc_input {

        width: 100%;
        font-size: medium;
        border: 0 !important;
        border-radius: 0;
        outline: 0;
        background: transparent !important;
        border-bottom: 1px solid black !important;
        display: block;
        text-align: center;

    }

    .calc_inputBox {
        width: 90%;
    }

    /* confimration for trade add to port */
    .calc_output_button_confirm {
        width: 70%;
        height: 30%;
    }

    /* not signed in for trade add button */
