}


.chartTab_timerDropDown:hover .fa-angle-up{
    color: red;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);     
}




@media screen and (max-width:960px) {

    .outerBox {


        width: 100%;
        z-index: 1;
        display: flex;
        justify-content: center;
